

























	var alertTimer = null;
	import graceLoading from "../../../graceUI/components/graceLoading.vue"
	import graceAlert from '../../../graceUI/components/graceAlert.vue';
	export default {
		components: {
			graceAlert,
			graceLoading
		},
		data() {
			return {
				confessionList: [],
				alert: {
					show: false,
					msg: '',
					msgtype: ''
				},
				loading: {
					show: true,
					nextPages: 2,
					endPages:2,
					type: 0,
					text: ['加载更多', 'loading ......', '已加载全部']
				}
			}
		},
		methods: {
			showMsg: function(type, msg) {
				this.alert.show = true;
				this.alert.msgtype = type;
				this.alert.msg = msg;
				//延迟关闭
				var _self = this;
				if (alertTimer != null) {
					clearTimeout(alertTimer);
				}
				alertTimer = setTimeout(function() {
					_self.alert.show = false;
					_self.alert.msg = '';
				}, 2000);
			}
		},
		onLoad: function() {
			this.confessionList = uni.getStorageSync('confessionList');
			uni.startPullDownRefresh();
		},
		onReady: function() {

		},
		//监听下拉刷新
		onPullDownRefresh: function() {
			uni.request({
				url: this.GLOBAL.serverSrc + 'confession/index/1',
				method: 'GET',
				success: res => {
					this.confessionList = res.data;
					this.showMsg('right', '表白墙动态已更新');
					uni.setStorage({
						key: 'confessionList',
						data: this.confessionList
					});
				},
				fail: () => {
					this.showMsg('error', '服务器异常，请稍后再试！');
				},
				complete: () => {
					uni.stopPullDownRefresh();
				}
			});
		},
		//监听触底加载事件
		onReachBottom: function() {
			//判断当前是否正在加载
            if (this.loading.type === 1){
                return;
            }
            //判断是否是最后一页
            if (this.loading.nextPages > this.loading.endPages){
                this.loading.type = 2;//全部
                return;
            }
            this.loading.type = 1;//加载中
			uni.request({
				url: this.GLOBAL.serverSrc + 'confession/index/' + this.loading.nextPages,
				method: 'GET',
				success: res => {
					this.confessionList = this.confessionList.concat(res.data);
				},
				fail: () => {
					this.showMsg('error', '服务器异常，请稍后再试！');
				},
				complete: () => {
					this.loading.type = 0;
					this.loading.nextPages++;
				}
			});
		}
	}
